'use client';

import { Button } from "@/components/ui/Button";
import { SearchIcon } from "lucide-react";

const HeroSection = () => {
    return (
        <div className="relative h-[600px] w-full flex items-center justify-center text-white">
            {/* Background Image */}
            <div
                className="absolute inset-0 bg-cover bg-center z-0"
                style={{ backgroundImage: "url('https://images.unsplash.com/photo-1562774053-701939374585?q=80&w=2070&auto=format&fit=crop')" }}
            >
                {/* Overlay */}
                <div className="absolute inset-0 bg-black/50"></div>
            </div>

            {/* Content */}
            <div className="relative z-10 flex flex-col items-center text-center px-4">
                <h1 className="text-5xl md:text-6xl font-extrabold !leading-tight tracking-tight">
                    Find your course materials
                </h1>
                <p className="mt-6 text-lg md:text-xl max-w-2xl text-gray-200">
                    Search for course codes or program names to access a wealth of study resources shared by your peers.
                </p>

                {/* Search Bar */}
                <div className="mt-8 flex w-full max-w-2xl items-center">
                    <div className="relative w-full">
                        <SearchIcon className="absolute left-6 top-1/2 -translate-y-1/2 h-6 w-6 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Enter course code or program name"
                            className="w-full h-16 pl-16 pr-40 rounded-full text-lg text-black"
                        />
                         <Button size="lg" className="absolute right-2 top-1/2 -translate-y-1/2 rounded-full h-12 px-8">
                            Search
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HeroSection; 